import React  from "react";
import '../style/style.css'

import colmena from '../img/bee-hive.png'
import {Temperatura} from '../components/graficos/temperatura'
import {Comida} from '../components/graficos/comida'
import {Peso} from '../components/graficos/peso'
import {Humedad} from '../components/graficos/humedad'
import Cookies from 'universal-cookie';
import url from '../constantes/constantes'
import {Buscar ,
  //  nombre_colmena_buscador
  } from '../components/buscador/buscador'
import axios from 'axios';
import {nombre_colmena} from './index'
const cookies = new Cookies();
 
class ColmenaData extends React.Component {
 
componentDidMount() {
  if(!cookies.get('token')){
    window.location.href="./";
}
  var token_token="token "+cookies.get('token');
  // console.log(nombre_colmena);
  if (nombre_colmena=== undefined){
    window.location.href="/inicio/";
  }
  
  const options_url = {
    headers: {
      'Content-Type': 'application/json',
      "Authorization": token_token,
    }
  };
  axios.post(url+"/datos/", {
    nombre:nombre_colmena
  },options_url)


  .then((response) => {
    
    let datos=response.data;
    console.log(datos);
    document.getElementById("fecha").innerHTML=datos.fecha;
    document.getElementById("t_int").innerHTML=datos.t_int+" C";
    document.getElementById("humedad").innerHTML=datos.humedad +" %";
    document.getElementById("peso").innerHTML=datos.peso  +" Kg";
    document.getElementById("comida").innerHTML=datos.comida ;
    document.getElementById("piquera").innerHTML=datos.piquera ;
    document.getElementById("revisado").innerHTML=datos.revision ;
  
  });

}
  render() {
       
  
      return (
    
        <>
        <Buscar/> 
        {/* <div className='barra_regreso'>
          <h4 className='volver_index' href=""><FaArrowLeft/></h4> <input className='input_buscar' placeholder="Nombre de colmena"/>  
          </div> */}
  
<div className='contenido_data'>
<div className="container_data">
  
<div className="item-left-data"> 
<div className="titulo_colmena" ><img className="image_colmena_data" src={colmena} alt="colmena"/><p className='nombre_data' >Colmena_1 </p>
</div>
<table className='tabla_datos_colmena'>
<tr>
<tr>Actualizado :</tr>
<tr id="fecha"> - </tr>
</tr>
<tr>
<tr>Temperatura interna :</tr>
<tr id="t_int"> C </tr>
</tr>
<tr>
<tr>Humedad :</tr>
<tr id="humedad"> %</tr>
</tr>
<tr>
<tr>Peso :</tr>
<tr id="peso">  Kg  </tr>
</tr>
<tr>
<tr>Comida :</tr>
<tr id="comida">  %</tr>
</tr>

<tr>
<tr>Piquera :</tr>
<tr id="piquera"> - </tr>
</tr>
<tr>
<tr>Revisado :</tr>
<tr id="revisado"> - </tr>
</tr>
</table>
</div>


<div className="item-right-data">


<table>
<tr>
<td><Temperatura/>
</td>
<td> <Humedad/>
</td>
</tr>
<tr>
<td><Peso/>
</td>
<td> <Comida/>
</td>
</tr>

</table>
</div>
</div>

</div>

</>
      );
  }
}

export { ColmenaData }; 

 
  