

import React from 'react';
 
import axios from 'axios'
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
 
} from 'chart.js';
import { Line } from 'react-chartjs-2';
import Cookies from 'universal-cookie';
import url from '../../constantes/constantes' 
import {nombre_colmena} from '../../pages/index'
const cookies = new Cookies();
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  
);

export class Temperatura extends React.Component {
  constructor(){
    super();
    this.state = {
      chartData:{},
      options:{},
      chartData2:{},
      options2:{}
    }
  }

  componentDidMount() {
    this.getChartData();
  }

  getChartData() {
    var token_token="token "+cookies.get('token');
    const options_url = {
             
            headers: {
              'Content-Type': 'application/json',
              "Authorization": token_token,
            }
          };
      
          axios.post(url+"/temperatura/", {
    
            nombre:nombre_colmena
                
                },options_url)
        .then(res => {
      
        let datos = res.data;
        let labels = datos.fechas;
        let valores = datos.t_int;
        let valores2 = datos.t_ext;
        

        // let toma1=[];
    
    
        // for (let i =0; i<valores.length; i++)  {
        //   toma1.push(valores[i][0]);
       
        // }
        
      this.setState({
        options: {   
          responsive: true,
          scales: {
            y:
              {
                min: 0,
                max: 50,
                stepSize: 2,
              },
            x:
              {
                
              },
          },
          plugins: {
            // legend: {
            //   display: false,
            // },
          title: {
              display: true,
              text:  'Temperatura Ext.(C) e Int.(C) vs Tiempo',
            },
          },       }
      });
        this.setState({
          chartData: {
            labels:labels,
            datasets: [
              {
                label: 'Temperatura interior C',
                data: valores,
                backgroundColor: '#4BC0C0',
                borderColor: '#4BC0C0',
              },
              {
                label: 'Temperatura Exterior C',
                data: valores2,
                backgroundColor: '#ff9700',
                borderColor: '#ff9700',
              }
             
            ]
          }
        });
      });
    }

  render(){

        return (
   
          <div className="tablas">
 {Object.keys(this.state.chartData).length &&
     
     <Line options={this.state.options} data={this.state.chartData}  style={{width:"430px"}}
     />}
  

          </div>
        );

    }     
}

export default Temperatura;