import React  from "react";
import '../style/style.css'
import {
  FaExclamationTriangle,
  FaPlus,
  FaSearch,FaChartBar
} from "react-icons/fa";
import colmena from '../img/bee-hive.png'

import { NavLink } from "react-router-dom";
import Cookies from 'universal-cookie';
import url from '../constantes/constantes'
import axios from 'axios';
const cookies = new Cookies();
export  var nombre_colmena;
function enviar_nombre(dato){
  nombre_colmena=dato;
}
class Index extends React.Component {
//   cerrarSesion=()=>{
//     cookies.remove('token', {path: "/"});
//     cookies.remove('user', {path: "/"});
//     window.location.href='./';
// }
 
componentDidMount() {
  if(!cookies.get('token')){
    window.location.href="./";
}
  var token_token="token "+cookies.get('token');
  // console.log(token_token);
  const options_url = {
    headers: {
      'Content-Type': 'application/json',
      "Authorization": token_token,
    }
  };
  axios.post(url+"/index_data/", {
     
  },options_url)


  .then((response) => {
    
    let datos=response.data;
    // console.log(datos);
     
    document.getElementById("tem_ext").innerHTML=datos.t_ext+"C";
    document.getElementById("clima").innerHTML=datos.clima ;
    document.getElementById("alertas").innerHTML=datos.error;
    document.getElementById("c1_t").innerHTML=datos.t_int+"C";
    document.getElementById("c1_r").innerHTML=datos.revision ;
    });

}
  render() {
      var nombre=cookies.get('user')
  
      return (
        <>
        <div className='index_center'>
        <div><h1 className='index_color'>Colmenas Del Centro Experimental Wayllapampa</h1></div>
        <div><h3 className='index_color'>Bienvenido {nombre}  </h3></div>
        <div className="container2">
    
        <div className="item-index-1"><h4>Temperatura Exterior </h4><h4 id="tem_ext">0</h4></div>
        <div className="item-index-2"> <h4>Precipitacin Pluvial</h4><h4 id="clima">-</h4></div>
        <div className="item-index-3"> <h4><FaExclamationTriangle/> - Alertas</h4><h4 id="alertas">0</h4></div>
        <div className="item-index-4"> <a className='agregar_colmenas' href="/">Agregar<FaPlus/></a></div>
        </div>
        </div>
        <div className='contenido_index'>
        <table border="0" className='logintabla'>
    
        <tbody>
  
        <tr>
        <td><input className='input_buscar' placeholder="Nombre de colmena"></input></td>
        <td> <h4 className='buscar_colmena' href=""><FaSearch/></h4></td>
  
        </tr>
  
        </tbody>
        </table>
        <table   className='tabla_colmenas'>
    
        <tbody>
  
        <tr>
        <th>Nombre de Colmena</th>
        <th> Temperatura Interior</th>
        <th> ltima Revisin</th>
        <th> Ver</th>
  
        </tr>
        <tr className='tabla_colmena'>
        <td className='nombre_colmena'> <img className="image_colmena" src={colmena} alt="colmena" /> <h3 className='nombre_colmena2'>Colmena_1 </h3> </td>
        <td className='nombre_colmena2 ' id="c1_t"> 0C</td>
        <td className='nombre_colmena2 'id="c1_r">  - </td>
        <td><NavLink to="/colmena_datos">  <a className='ver_colmenas' href="/colmena_datos" on onClick={enviar_nombre("colmena_1")}>Ver <FaChartBar/></a></NavLink></td>
  
        </tr>
        <tr className='tabla_colmena'>
        <td className='nombre_colmena'> <img className="image_colmena" src={colmena} alt="colmena" /> <h3 className='nombre_colmena2'>Colmena_2 </h3> </td>
        <td className='nombre_colmena2 '  > 0C</td>
        <td className='nombre_colmena2 ' >  - </td>
        <td></td>
  
        </tr>
        </tbody>
        </table>
  
        </div>
          </>
      );
  }
}

export { Index }; 