import React from "react";
import '../style/style.css'
import bee from '../img/bee.png';
import url from '../constantes/constantes'
import axios from 'axios';
import Cookies from 'universal-cookie';
const cookies = new Cookies();
 

class Login extends React.Component {
//   state={
//     form:{
//         username: '',
//         token: ''
//     }
// }
// handleChange=async e=>{
//   await this.setState({
//       form:{
//           ...this.state.form,
//           [e.target.name]: e.target.value
//       }
//   });
// }
  
  render() {
    function Enviar(){
      let user=document.getElementById("user_name").value;
      let pass=document.getElementById("password_name").value;
      let url_login=url+"/login_web/"
      const options_url = {
        headers: {
          'Content-Type': 'application/json',
         
        }
      };
     
      axios.post(url_login, 
      {
        usuario: user,
        password:pass
      },options_url)
      .then((response) => {
    
        let datos=response.data;
        let token=datos.token;
        let user_name=datos.nombre;
        
        var value=datos.value;
        if (value==="correcto"){
          cookies.set('token', token, {path: "/"});
          cookies.set('user', user_name, {path: "/"});
          window.location.href = './inicio';
        }
        else{
          console.log("alert")
        }
        });
     
     };
    
  return (

  <>

  <div className="container">

  <div className="item-left">


  <h2 ><img src={bee} alt="bee"  width="50" height="50"/>&nbsp;Bienvenido a Bee Project !</h2>

  <table border="0" className='logintabla'>

  <tbody>

  <tr>
  <th>Usuario:</th>
  <td><input type="text" id="user_name" name="username"  onChange={this.handleChange}></input></td>

  </tr>
  <tr>
  <th>Contrasea:</th>
  <td><input type="password"  id="password_name" name="password"></input></td>

  </tr>
  <tr>
  <th> </th>
  <td> <input type="submit" value="Login" onClick={Enviar}></input></td>

  </tr>

  </tbody>
  </table>


  </div>


  <div className="item-right">  


  <div className="carousel-wrapper">
  <span id="item-1"></span>
  <span id="item-2"></span>
  <span id="item-3"></span>
  <div className="carousel-item item-1">
  <a href="#item-3" className="arrow-prev arrow"> </a>
  <a href="#item-2" className="arrow-next arrow"> </a>
  </div>

  <div className="carousel-item item-2">
  <a href="#item-1" className="arrow-prev arrow"> </a>
  <a href="#item-3" className="arrow-next arrow"> </a>
  </div>

  <div className="carousel-item item-3">
  <a href="#item-2" className="arrow-prev arrow"> </a>
  <a href="#item-1" className="arrow-next arrow"> </a>
  </div>

  </div>

  </div>
  </div>

  </>
  );
  }
  }

export { Login }; 