import React from "react";
import { BrowserRouter, Route ,Routes} from 'react-router-dom';
 
import NavbarLogin from './components/navbar_login/Navbar_login'
import Navbar from './components/navbar_app/Navbar'
import Footer from './components/footer/Footer'
import {Login} from './pages/Login';
import Nosotros from './pages/nosotros';
import Instrucciones from './pages/instrucciones'
import {Index} from './pages/index'
import Footerlogin from './components/footer/Footer_login'
import {ColmenaData} from './pages/colmena';
export default function App() {
    return (
<div>

  <BrowserRouter>
    {/* RUTAS DEL NAVBAR */}
    <Routes>
      <Route exact path="/" element={<NavbarLogin/> } />
      <Route exact path="/inicio" element={<Navbar/>} />
      <Route exact path="/instrucciones" element={<Navbar/>} />
      <Route exact path="/nosotros" element={<NavbarLogin/>} />
      <Route exact path="/nosotros_in" element={<Navbar/>} />
      <Route exact path="/colmena_datos" element={<Navbar/>} />
      <Route exact path="/error" element={<Navbar/>} />
      <Route path="*" element={<NavbarLogin/>} />
    </Routes>
   {/* RUTAS DEL CONTENIDO */}
      <Routes>
      <Route exact path="/" element={<Login/>} />
      <Route exact path="/inicio" element={<Index/>} />
      <Route exact path="/instrucciones" element={<Instrucciones/>} />
      <Route exact path="/nosotros" element={<Nosotros/>} />
      <Route exact path="/nosotros_in" element={<Nosotros/>} />
      <Route exact path="/colmena_datos" element={<ColmenaData/>} />
      <Route exact path="/error" element={<ColmenaData/>} />
      <Route path="*" element={<div><h1>No encontro Pagina</h1></div>} />
    </Routes>
    <Routes>
      <Route exact path="/" element={<Footerlogin/>} />
      <Route exact path="/inicio" element={<Footer/>} />
      <Route exact path="/instrucciones" element={<Footer/>} />
      <Route exact path="/nosotros" element={<Footerlogin/>} />
      <Route exact path="/nosotros_in" element={<Footer/>} />
      <Route exact path="/colmena_datos" element={<Footer/>} />
      <Route exact path="/error" element={<Footer/>} />
      <Route path="*" element={<Footer/>} />
    </Routes>
   
  </BrowserRouter>
  </div>
    );
  }